<template>
  <div class="flex flex-col space-y-4">
    <steps :steps="steps" :step="step"></steps>
    <div class="flex flex-col space-y-8" v-if="step == 1">
      <div
        v-if="quagging && !codeFound"
        class="relative py-3 pl-4 pr-10 leading-normal text-yellow-700 bg-yellow-100 rounded-lg mt-5 text-base"
        role="alert"
      >
        <p>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-6 w-6 animate-ping inline-flex rounded-full bg-green-500 opacity-90 mr-2"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M8.228 9c.549-1.165 2.03-2 3.772-2 2.21 0 4 1.343 4 3 0 1.4-1.278 2.575-3.006 2.907-.542.104-.994.54-.994 1.093m0 3h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
            /></svg
          >Suche Barcode...
        </p>
      </div>
      <div v-show="quagging" class="" id="code-scanner">
        <video class="w-60 mb-10"></video>
        <canvas class="drawingBuffer absolute left-0 top-0"></canvas>
      </div>
      <div class="flex space-x-2">
        <div class="flex flex-col space-y-1">
          <label class="text-white text-sm" for="productCode">Produkt-Code</label>
          <input
            name="productCode"
            v-model="productCode"
            placeholder="Produkt-Code"
            class="rounded-3xl border border-gray-500 focus:outline-none focus:ring focus:ring-green-500 px-2 py-1"
            type="text"
          />
        </div>
        <button @click="startScanning()" class="rounded bg-green-500 hover:bg-green-600 text-white py-2 px-3">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="object-contain w-8"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M12 4v1m6 11h2m-6 0h-2v4m0-11v3m0 0h.01M12 12h4.01M16 20h4M4 12h4m12 0h.01M5 8h2a1 1 0 001-1V5a1 1 0 00-1-1H5a1 1 0 00-1 1v2a1 1 0 001 1zm12 0h2a1 1 0 001-1V5a1 1 0 00-1-1h-2a1 1 0 00-1 1v2a1 1 0 001 1zM5 20h2a1 1 0 001-1v-2a1 1 0 00-1-1H5a1 1 0 00-1 1v2a1 1 0 001 1z"
            />
          </svg>
        </button>
        <button
          v-if="quagging && !codeFound"
          @click="stopScanning()"
          class="rounded bg-red-400 hover:bg-red-600 text-white py-2 px-3"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="object-contain w-8"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>
      <div class="flex space-x-2 justify-end mt-20">
        <button
          :disabled="productCode == ''"
          @click="nextStep()"
          class="rounded bg-green-500 hover:bg-green-600 py-2 px-3 disabled:opacity-50 text-white"
        >
          Weiter
        </button>
      </div>
    </div>
    <div class="flex flex-col space-y-4" v-if="step == 2">
      <div class="flex flex-col space-y-1">
        <label class="text-white text-sm" for="productCode">Produkt-Code</label>
        <input
          disabled
          name="productCode"
          v-model="productCode"
          placeholder="Produkt-Code"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-50
            text-white
          "
          type="text"
        />
      </div>
      <div class="flex flex-col space-y-1">
        <label class="text-white text-sm" for="productName">Produktname</label>
        <input
          name="productName"
          v-model="productName"
          placeholder="Produktname"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
          type="text"
        />
      </div>
      <div class="flex flex-col space-y-1">
        <label class="text-white text-sm" for="productGram">Einheit in Gramm?</label>
        <input
          name="productGram"
          type="checkbox"
          v-model="productGram"
          placeholder="Produktname"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
        />
      </div>
      <div class="flex flex-col space-y-1">
        <label v-if="productGram" class="text-white text-sm" for="productPrice">Preis pro 100g</label>
        <label v-else class="text-white text-sm" for="productPrice">Preis</label>
        <input
          name="productPrice"
          v-model="productPrice"
          :placeholder="productGram ? 'Preis pro 100g' : 'Preis'"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
          type="number"
        />
      </div>
      <div class="flex flex-col space-y-1">
        <label v-if="productGram" class="text-white text-sm" for="productAmount">Menge in g</label>
        <label v-else class="text-white text-sm" for="productAmount">Anzahl</label>
        <input
          name="productAmount"
          v-model="productAmount"
          :placeholder="productGram ? 'Menge in g' : 'Anzahl'"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
          type="number"
        />
      </div>
      <div class="flex flex-col space-y-1">
        <label class="text-white text-sm" for="productMhd">MHD</label>
        <input
          name="productMhd"
          v-model="productMhd"
          placeholder="MHD"
          class="
            rounded-3xl
            border border-gray-500
            focus:outline-none focus:ring focus:ring-green-500
            px-2
            py-1
            disabled:opacity-80
          "
          type="date"
        />
      </div>
      <div class="flex space-x-2">
        <button @click="previousStep()" class="rounded bg-gray-500 hover:bg-gray-600 py-2 px-3 text-white">
          Zur端ck
        </button>
        <button
          :disabled="missingValues"
          @click="addProduct()"
          class="rounded bg-green-500 hover:bg-green-600 py-2 px-3 disabled:opacity-75 text-white"
        >
          Hinzuf端gen
        </button>
      </div>
    </div>
    <div class="flex flex-col justify-center space-y-2" v-if="step == steps + 1">
      <div class="text-white text-center">Produkt(e) erfolgreich hinzugef端gt!</div>
      <button @click="newProduct()" class="rounded bg-green-500 hover:bg-green-600 py-2 px-3 text-white">
        Neue(s) Produkt(e) hinzuf端gen
      </button>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from "vue";
import Steps from "@/components/Steps.vue";
import Quagga from "@ericblade/quagga2";
import { ApiRequest, API_URLS } from "@/api/request.api";

export default defineComponent({
  name: "Wareneingang",
  components: { Steps },
  setup() {
    const step = ref(1);
    const steps = ref(2);
    const productCode = ref("");
    const productName = ref("");
    const productGram = ref(false);
    const productPrice = ref(0);
    const productAmount = ref(0);
    const productMhd = ref(new Date());
    const codeFound = ref(false);
    const productFound = ref(false);
    const quagging = ref(false);

    const missingValues = computed(() => {
      return !productName.value || !productPrice.value || !productAmount.value || !productMhd.value;
    });

    const nextStep = () => {
      step.value = step.value + 1;
    };

    const previousStep = () => {
      step.value = step.value > 1 ? step.value - 1 : step.value;
    };

    const startScanning = () => {
      quagging.value = true;
      Quagga.init(
        {
          locate: true,
          frequency: 5,
          numOfWorkers: 2,
          locator: { halfSample: true, patchSize: "medium" },
          inputStream: {
            name: "Live",
            type: "LiveStream",
            target: document.getElementById("code-scanner") || undefined,
          },
          decoder: {
            readers: ["ean_reader"],
          },
        },
        function (err) {
          if (err) {
            console.error(err);
            return;
          }
          Quagga.start();
        }
      );
    };

    const stopScanning = () => {
      quagging.value = false;
      Quagga.stop();
    };

    Quagga.onProcessed((data) => {
      if (data && data.codeResult && data.codeResult.code) {
        codeFound.value = true;
      }
    });

    Quagga.onDetected((data) => {
      productCode.value = data.codeResult.code ? data.codeResult.code : "";
      quagging.value = false;
      Quagga.stop();
    });

    const addProduct = async () => {
      if (!productFound.value) {
        try {
          await new ApiRequest(API_URLS.ADD_NEW_INVENTORY_ENTRY)
            .post()
            .body({
              product_code: productCode.value,
              product_name: productName.value,
              amount: productAmount.value,
              is_gram: productGram.value,
              price_tag: productPrice.value,
              good_till: productMhd.value,
            })
            .execute();
          step.value += 1;
        } catch (error) {
          console.error(error);
        }
      }
    };

    const newProduct = () => {
      step.value = 1;
      productCode.value = "";
      productName.value = "";
      productAmount.value = 0;
      productGram.value = false;
      productPrice.value = 0;
      productMhd.value = new Date();
    };

    return {
      startScanning,
      stopScanning,
      productCode,
      productName,
      productGram,
      productPrice,
      productAmount,
      productMhd,
      quagging,
      codeFound,
      step,
      steps,
      nextStep,
      previousStep,
      missingValues,
      addProduct,
    };
  },
});
</script>
